<template>
  <b-row>
    <b-col sm="12" md="4" lg="3" v-for="category in categories" :key="category.id">
      <b-card>
        <h4>{{ category.name.trim() }}</h4>
        <Sections :category_id="category.id" />
      </b-card>
    </b-col>
  </b-row>
</template>

<script>
  import Categories from "@/js/support/cname/categories";
  import Sections from "./Sections";

  export default {
    components: { Sections },
    data() {
      return {
        categories: "",
      };
    },

    async mounted() {
      // Get categories
      this.categories = await Categories.get_categories();
    },
  };
</script>
