import Data from "@/js/core/data";

const Categories_Server = "https://api.${region}.500apps.com/support/kb/${window.domain}/${window.user}/kb_categories";

class Categories extends Data {
  constructor() {
    super(Categories_Server);
    this.disablePCors();
  }

  async get_categories() {
    let url = Categories_Server;
    this.url = eval("`" + url + "`");
    return await super.get();
  }
}
export default new Categories();
