<template>
  <div>
    <b-spinner v-if="loading" variant="primary" label="Spinning"></b-spinner>

    <div v-else>
      <BreadCrumbs v-if="total_articles.length" v-bind="{ ...total_articles[0] }" />
      <h4 class="text-capitalize border-bottom pb-3" v-if="selected_article.length">{{ selected_article[0].name }}</h4>
      <div v-if="selected_article" v-html="selected_article.content"></div>
    </div>
  </div>
</template>
<script>
  import BreadCrumbs from "./BreadCrumbs";
  import Articles from "@/js/support/cname/articles";
  export default {
    data() {
      return {
        selected_article: "",
        total_articles: "",
        loading: false,
      };
    },
    components: { BreadCrumbs },
    async mounted() {
      this.loading = true;
      await this.getArticles(this.$route.params.c_id, this.$route.params.s_id);
      this.loading = false;
    },

    methods: {
      // Get article data under given section & categorys
      async getArticles(category_id, section_id) {
        this.total_articles = await Articles.get_Articles(category_id, section_id);
        // Find selected article
        this.selected_article = this.total_articles.find((data) => data.id == this.$route.params.a_id);
      },
    },
  };
</script>
