import Categories from "./Categories";
import Category from "./Category";
import ArticlePreview from "./ArticlePreview";
import BreadCrumbs from "./BreadCrumbs";
import Articles from "./Articles";
import Header from "./Header";
import Footer from "./Footer";
import KBTemplate from "@/js/support/cname/kb-templates";
export default {
  name: "Element",
  render(h) {
    return h({
      template: this.template,
      components: { Header, Footer, Categories, Category, ArticlePreview, Articles, BreadCrumbs },
    });
  },
  data() {
    return {
      template: `<div><Header/><router-view/><Footer/></div>`,
    };
  },
  async created() {
    try {
      let data = await KBTemplate.get();
      if (data.length) {
        data = data.find((data) => data.kb_template.includes("<Header/>"));
        if (data?.kb_template) this.template = `<div>${data[0].kb_template.replace("Categories", "router-view")}</div>`;
      }
    } catch (error) {
      console.log(error);
    }
  },
};
