<template>
  <div>
    <b-spinner v-if="loading" variant="primary" label="Spinning"></b-spinner>
    <div v-else>
      <b-link class="d-block mb-2" v-for="section in sections" :key="section.id" @click="showArticles(category_id, section.id, section.name)">{{ section.name.trim() }} </b-link>
    </div>
  </div>
</template>

<script>
  import Sections from "@/js/support/cname/sections";

  export default {
    data() {
      return {
        sections: [],
        loading: false,
      };
    },
    props: {
      category_id: Number,
    },
    methods: {
      // Get All sections list under a category
      async getSections(category_id) {
        this.sections = await Sections.get(category_id);
      },

      showArticles(category_id, section_id, name) {
        this.$router.push({
          name: "Sections",
          params: {
            section_name: name
              .trim()
              .toLowerCase()
              .replaceAll(" ", "-"),
            s_id: section_id,
            c_id: category_id,
          },
        });
      },
    },
    async mounted() {
      this.loading = true;
      await this.getSections(this.category_id);
      this.loading = false;
    },
  };
</script>
