<template>
  <b-container class="mt-4">
    <b-row>
      <b-spinner v-if="loading" variant="primary" label="Spinning"></b-spinner>
      <b-col sm="12" v-else>
        <BreadCrumbs v-if="total_articles.length" v-bind="{ ...total_articles[0] }" />
        <h4 class="text-capitalize border-bottom pb-3" v-if="total_articles.length">{{ total_articles[0].category_name }}</h4>
        <div v-for="section in sections" :key="section.id">
          <span class="fe fe-chevron-right mr-2 "></span>
          <b-link @click="showArticles($route.params.c_id, section.id, section.name)">{{ section.name }}</b-link>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  import BreadCrumbs from "./BreadCrumbs";
  import Articles from "@/js/support/cname/articles";
  export default {
    components: { BreadCrumbs },
    data() {
      return {
        total_articles: "",
        sections: "",
        loading: false,
      };
    },
    props: {},

    async mounted() {
      this.loading = true;
      await this.getArticles(this.$route.params.c_id);
      this.loading = false;
    },
    methods: {
      // Navigate to selected sections with corresponding data
      showArticles(category_id, section_id, name) {
        this.$router.push({
          name: "Sections",
          params: {
            section_name: name
              .trim()
              .toLowerCase()
              .replaceAll(" ", "-"),
            s_id: section_id,
          },
          query: { c_id: category_id },
        });
      },
      // Get total articles
      async getArticles(category_id) {
        this.total_articles = await Articles.get_Articles(category_id);
        // Filter all sections list under a selected category
        this.sections = this.total_articles.filter((article) => article.category_id == category_id);
      },
    },
  };
</script>
